.app {
  text-align: center;
  color: #222;
}

.header {
  background-color: #e4e3e3;
  height: 50px;
  width: 100vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.header .logo {
  height: 40px;
  width: 40px;
  margin: 5px;
}

.header .title {
  font-size: 25px;
  line-height: 40px;
  margin: 5px;
  text-align: center;
}

.box {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: calc(100vh - 50px);
}

.box .prompt {
  font-size: 22px;
  text-align: center;
  margin: 20px 0px;
  font-weight: bold;
}

.box button {
  height: 50px;
  min-width: 60vw;
  font-size: 20px;
  line-height: 50px;
  border: 0px solid #fff;
  padding: 0px 20px;
  margin: 10px 0px;
  color: #222;
  background-color: #e7c9e6;
  border-radius: 5px;
  outline: none;
  cursor: pointer;
}

.box input {
  height: 50px;
  min-width: 60vw;
  font-size: 20px;
  line-height: 50px;
  border: 3px solid #333;
  padding: 10px;
  margin: 10px 0px;
  color: #222;
  background-color: #fff;
  border-radius: 5px;
  outline: none;
  cursor: pointer;
}

.box .check {
  border-radius: 10px;
  height: 40px;
  font-size: 20px;
  line-height: 40px;
  text-align: center;
  padding: 0px 20px;
}

.box .check.r {
  border: 5px solid #abd6ab;
}

.box .check.w {
  border: 5px solid #d6abab;
}

.barcode {
  width: 35vw;
  height: 50px;
  display: -ms-flexbox;
  display: flex;
  border: 2px solid rgb(224, 87, 87);
}

.barcode .bar {
  height: 50px;
  width: 5vw;
}


